import React,  {useState} from 'react';
import './index.scss'





import HomeImg from '../../img/icons/home.svg'


import Services from './services';
import Maps from './regions';
import Problems from './problems';
import Gallery from './gallery';
import Contratos from './contracts';
const Home = (props) => {
    
    if(props.name=='services'){
        setTimeout(function(){
            let element = document.getElementById("services");
            element.scrollIntoView({block: "start", behavior: "smooth"})
        }, 200);
    }
   
    if(props.name=='problems'){
        setTimeout(function(){
            let element = document.getElementById("problems");
            element.scrollIntoView({block: "start", behavior: "smooth"})
        }, 200);
    }

    if(props.name=='gallery'){
        setTimeout(function(){
            let element = document.getElementById("gallery");
            element.scrollIntoView({block: "start", behavior: "smooth"})
        }, 200);
    }
   

    return(
        <div className="homeContainer">
            <section className="fondoCoche" 
            id='fondo'
            >
                <img src={HomeImg}></img>
            </section>
            <div id='services'>
                <Services></Services>
            </div>
            <div id='maps'>
                <Maps></Maps>   
            </div>
            <div id='problems'>
                <Problems></Problems>               
            </div>
            <Contratos></Contratos>
            <div id='gallery'>
                <Gallery></Gallery>
            </div>
        </div>
    )
}

export default Home;