import React, {useState} from "react";
import './index.scss'


const Problems = () =>{
    const [problem, setProblem] = useState('0')
   

    return(
        <section className="commonProblems">
            <section className="Problems">
                <h2>COMMON PROBLEMS</h2>                
                {problem === '1'? <label className="optionProblem"><text>DEBRIS</text></label>:<label><text onClick={()=>setProblem('1')}>DEBRIS</text></label>}
                {problem == '2'? <label className="optionProblem"><text>GREASE DEPOSITS</text></label>:<label><text onClick={()=>setProblem('2')}>GREASE DEPOSITS</text></label>}
                {problem == '3'? <label className="optionProblem"><text >TREE ROOTS</text></label>:<label><text onClick={()=>setProblem('3')}>TREE ROOTS</text></label>}
                {problem == '4'? <label className="optionProblem"><text>BROKEN PIPE</text></label>:<label><text onClick={()=>setProblem('4')}>BROKEN PIPE</text></label>}
                {problem == '5'? <label className="optionProblem"><text>OFFSET PIPE</text></label>:<label><text onClick={()=>setProblem('5')}>OFFSET PIPE</text></label>}
                {problem == '6'? <label className="optionProblem"><text >BELLY</text></label>:<label><text onClick={()=>setProblem('6')}>BELLY</text></label>}
            </section>
            <section className="Images">
                    {problem == '1'? <section className="Problema1"></section>:''}
                    {problem == '2'? <section className="Problema2"></section>:''}
                    {problem == '3'? <section className="Problema3"></section>:''}
                    {problem == '4'? <section className="Problema4"></section>:''}
                    {problem == '5'? <section className="Problema5"></section>:''}
                    {problem == '6'? <section className="Problema6"></section>:''}
                    {problem == '0'? <section className="Problema7"></section>:''}
            </section>
        </section>    
    )
}

export default Problems